body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: gray;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.none {
  display: none !important;
}
.container {
  box-sizing: border-box;
  width: 100vw;
  padding: 20px 80px;
}

.backtop {
  position: fixed;
  cursor: pointer;
  right: 20px;
  bottom: 20px;
  width: 50px;
  height: 50px;
  background-color: black;
}
.content-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
}

.content-tabs {
    width: 200px;
}

.content-tabs-item {
    cursor: pointer;
    width: 100%;
    box-sizing: border-box;
    padding: 10px 5px;
    background-color: white;
    margin: 5px 0;
}

.content-scenesList {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 85%;
    background-color: rgb(196, 196, 196);
}

.content-scenesList-item {
    width: 330px;
    height: 200px;
    border: 1px solid black;
    background-color: white;
    margin-bottom: 10px;
}

.popUp-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .7);

}

.popUp-close {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
}

.popUp-display {
    display: flex;
    flex-direction: column;
    width: 500px;
    max-height: 700px;
    background-color: white;
    border-radius: 10px;
    padding: 20px;
}

.popUp-display :nth-child(1) {
    font-size: 28px;
}

.popUp-display :nth-child(2) {
    font-size: 24px;
}

.popUp-display :nth-child(3) {
    font-size: 24px;
}

.popUp-display :nth-child(4) {
    font-size: 18px;
    overflow-y: scroll;
}

.popUp-item {
    padding: 10px 0;
}
.carousel-container {
    position: relative;
    width: 100%;
    height: 300px;
    background-color: rgb(179, 179, 179);
}

.carousel-content {
    width: 100%;
    height: 100%;
}

.carousel-content-img {
    width: 100%;
    height: 100%;
}

.carousel-arrow-left {
    cursor: pointer;
    width: 30px;
    height: 30px;
    border: 3px solid white;
    border-bottom: 0;
    border-right: 0;
    position: absolute;
    left: 20px;
    bottom: 50%;
    -webkit-transform: translateY(50%) rotate(-45deg);
            transform: translateY(50%) rotate(-45deg);
}

.carousel-arrow-right {
    cursor: pointer;
    width: 30px;
    height: 30px;
    border: 3px solid white;
    border-bottom: 0;
    border-right: 0;
    position: absolute;
    right: 20px;
    bottom: 50%;
    -webkit-transform: translateY(50%) rotate(135deg);
            transform: translateY(50%) rotate(135deg);
}

.carousel-dot-group {
    position: absolute;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    left: 50%;
    bottom: 10%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.carousel-dot {
    width: 10px;
    height: 10px;
    border-radius: 5px;
    margin: 0 5px;
    background-color: white;
    cursor: pointer;
}
.searchbar-container {
    box-sizing: border-box;
    width: 100%;
    padding: 20px;
    height: 80px;
    z-index: 1;
}

.searchbar-container input {
    width: 700px;
    padding: 10px 20px;
    font-size: 16px;
    background-color: white;
    border: 1px solid black;
}

.searchbar-container-sticky {
    position: fixed;
    z-index: 1;
    top: 20px;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    outline: none;
}
.countdown-container {
    width: 100%;
    box-sizing: border-box;
    background-color: white;
    margin: 20px 0;
    padding: 20px 5px;
}
